<template>
  <Header />
  <AddTodo @add-todo="addobj" :todos="todos" />
  <Todos :todos="todos" @del-todo="delmethod" />
</template>

<script>
  import Todos from "./components/Todos";
  import AddTodo from "./components/AddTodo";
  import Header from "./components/layout/Header";
  export default {
    name: "App",

    data() {
      return {
        todos: [
          {
            id: 1,
            title: "Todo One",
            completed: true,
          },
          {
            id: 2,
            title: "Todo Two",
            completed: true,
          },
          {
            id: 3,
            title: "Todo Third",
            completed: true,
          },
        ],
      };
    },
    methods: {
      delmethod(todo) {
        this.todos = this.todos.filter((x) => x.id !== todo.id);
        console.log(todo);
      },
      addobj(obj) {
        this.todos = [...this.todos, obj];
        console.log(this.todos);
      },
    },
    components: {
      Todos,
      Header,
      AddTodo,
    },
  };
</script>

