<template>
  <div class="todo-item" :class="{ complete: todo.completed }">
    <input
      @click="callback(todo)"
      style="display: inline-flex;float: left;margin-top: 16px;"
      type="checkbox"
    />
    <button @click="$emit('del-todo', todo)" class="del">x</button>
    <p style="display: inline-flex;">{{ todo.title }}</p>
  </div>
</template>

<script>
export default {
  name: "TodoItem",
  props: ["todo"],
  data() {
    return {};
  },
  methods: {
    callback(todo) {
      todo.completed = !todo.completed;
    },
  },
};
</script>

<style lang="css" scoped>
.todo-item {
  background-color: #f4f4f4;
  text-align: center;
  text-transform: capitalize;
  padding: 10px;
  border-bottom: 1px #ccc dotted;
  max-width: 50%;
}

.complete {
  text-decoration: line-through;
}
.del {
  background: #ff0000;
  color: #fff;
  border: none;
  padding: 5px 9px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
  margin-top: 10px;
}
</style>
