<template>
  <form @submit.prevent="addtodoobj">
    <input
      v-model="title"
      type="text"
      name="addtodo"
      id="addtodo"
      placeholder="AddTodo..."
    />
    <input class="btn" type="submit" value="submit" />
  </form>
  <h1>{{ title }}</h1>
</template>

<script>
  export default {
    name: "AddTodo",

    data() {
      return {
        title: "",
      };
    },
    props: ["todos"],
    methods: {
      addtodoobj() {
        const obj = {
          id: this.todos.length + 1,
          title: this.title,
          complted: false,
        };
        this.$emit("add-todo", obj);
        this.title = "";
      },
    },
  };
</script>

